<template>
  <div class="self-root-div"
       :ref="ref.rootDiv"
       :style="divStyle">
    <!--展示区-->
    <div class="self-buttons" :ref="ref.buttons">
      <Button type="primary" size='small'
              style="margin-right: 45px" @click="web_add">
        新增网站
      </Button>
      <Divider style="margin: 4px 0px 0px 0px"></Divider>
    </div>
    <div :style="collapseStyle">
      <Collapse v-model="unfold"
                :ref="ref.collapse"
                accordion
                @on-change="collapseChange"
                :key="index"
                v-for="(web, index) of page.content">
        <Panel style="text-align: left;"
               :name="panelNamePrefix + index"
               :ref="panelNamePrefix + index"
               :key="web.url">
          <span>{{web.title}}</span>
          <Badge :count="webNewNum(web)" type="success" style="margin-left: 8px"></Badge>
          <span style="position: absolute;left: 35%;">
            {{web.url}}
          </span>
          <div>
            <Button type="primary"
                    @click.stop="web_modify(web)"
                    size='small'
                    style="right: 150px"
                    class="self-button">修改</Button>
            <Button type="primary"
                    @click.stop="web_del(web)"
                    size='small'
                    style="right: 100px"
                    class="self-button">删除</Button>
            <Button type="primary"
                    @click.stop="category_add(web.id)"
                    size='small'
                    style="right: 25px"
                    class="self-button">新增分类</Button>
          </div>
          <div slot="content">
            <Table :border="true"
                   :columns="columns"
                   :data="web.categories"
                   :show-header="true"
                   :stripe="true"
                   :highlight-row="true"
                   style="border:none"
                   size="small">
            </Table>
          </div>
        </Panel>
      </Collapse>
    </div>
    <!--展示区-->
    <!--网站处理区-->
    <SelfModalForm v-model="web.form.modal"
                   :title="web.form.title"
                   :url="web.form.url"
                   :items="web.form.items"
                   :rules="web.form.rules"
                   :extraParams="web.form.extraParams"
                   @self-done="reload">
      <div slot="fields">
        <Input type="text" style="display: none;" v-model="web.form.items.userId"></Input>
        <FormItem :label="web.form.labels.title" prop="title">
          <Row>
            <Col span="20">
              <Input type="text" clearable v-model="web.form.items.title"
                     :placeholder="web.form.labels.title"></Input>
            </Col>
          </Row>
        </FormItem>
        <FormItem :label="web.form.labels.url" prop="url">
          <Row>
            <Col span="20">
              <Input type="text" clearable v-model="web.form.items.url"
                     :placeholder="web.form.labels.url"></Input>
            </Col>
          </Row>
        </FormItem>
      </div>
    </SelfModalForm>
    <SelfModalState v-model="web.del.modal"
                  :url="web.del.url"
                  :params="web.del.params"
                  @self-done="reload">
      <div slot="msg" style="text-align: center">
        <p>{{web.del.msg}}</p>
      </div>
    </SelfModalState>
    <!--网站处理区-->
    <!--分类处理区-->
    <SelfModalForm v-model="category.form.modal"
                   :title="category.form.title"
                   :url="category.form.url"
                   :items="category.form.items"
                   :rules="category.form.rules"
                   :extraParams="category.form.extraParams"
                   @self-done="reload">
      <div slot="fields">
        <Input type="text" style="display: none" v-model="category.form.items.webId"></Input>
        <FormItem :label="category.form.labels.title" prop="title">
          <Row>
            <Col span="20">
              <Input type="text" clearable v-model="category.form.items.title"
                     :placeholder="category.form.labels.title"></Input>
            </Col>
          </Row>
        </FormItem>
        <FormItem :label="category.form.labels.url" prop="url">
          <Row>
            <Col span="20">
              <Input type="text" clearable v-model="category.form.items.url"
                     :placeholder="category.form.labels.url"></Input>
            </Col>
          </Row>
        </FormItem>
        <FormItem :label="category.form.labels.xpathTitle" prop="xpath">
          <Row>
            <Col span="20">
              <Input type="text" clearable v-model="category.form.items.xpathTitle"
                     :placeholder="category.form.labels.xpathTitle"></Input>
            </Col>
          </Row>
        </FormItem>
        <FormItem :label="category.form.labels.xpathText" prop="xpath">
          <Row>
            <Col span="20">
              <Input type="text" clearable v-model="category.form.items.xpathText"
                     :placeholder="category.form.labels.xpathText"></Input>
            </Col>
          </Row>
        </FormItem>
        <FormItem :label="category.form.labels.charset" prop="xpath">
          <Row>
            <Col span="20">
              <Input type="text" clearable v-model="category.form.items.charset"
                     :placeholder="category.form.labels.charset"></Input>
            </Col>
          </Row>
        </FormItem>
      </div>
    </SelfModalForm>
    <SelfModalState v-model="category.del.modal"
                  :url="category.del.url"
                  :params="category.del.params"
                  @self-done="reload">
      <div slot="msg" style="text-align: center">
        <p>{{category.del.msg}}</p>
      </div>
    </SelfModalState>
    <!--分类处理区-->
    <!--分页区-->
    <Page :total='page.totalElements'
          :ref="ref.page"
          class="self-page"
          show-sizer
          @on-change="pageChange"
          @on-page-size-change="pageSizeChange"/>
    <!--分页区-->
  </div>
</template>
<script>
export default {
  props: ['params'],
  data () {
    return {
      url: {
        web: {
          query: '/crawler/findWebs',
          add: '/crawler/addWeb',
          modify: '/crawler/modifyWeb',
          del: '/crawler/deleteWeb',
          hasRead: '/crawler/categoryHasRead',
        },
        category: {
          add: '/crawler/addCategory',
          modify: '/crawler/modifyCategory',
          del: '/crawler/deleteCategory'
        }
      },
      unfold: 'p_0',
      panelNamePrefix: 'p_',
      pageNumber: 0,
      pageSize: 10,
      page: {},
      collapseStyle: {},
      ref: {
        rootDiv: 'rootDiv',
        page: 'page',
        buttons: 'buttons',
        collapse: 'collapse'
      },
      web: {
        form: {
          modal: false,
          title: '',
          url: '',
          labels: {
            url: '网站链接',
            title: '网站标题'
          },
          items: {
            userId: this.$store.getters.userInfo.id,
            url: '',
            title: ''
          },
          rules: {
            url: [
              {required: true, message: '请输入链接', trigger: 'blur'}
            ],
            title: [
              {required: true, message: '请输入标题', trigger: 'blur'}
            ]
          },
          extraParams: {}
        },
        del: {
          modal: false,
          url: '',
          msg: '',
          params: null
        },
      },
      category: {
        form: {
          modal: false,
          title: '',
          url: '',
          labels: {
            webId: '网站ID',
            title: '分类标题',
            url: '分类链接',
            xpathTitle: '标题路径',
            xpathText: '正文路径',
            charset: '编码'
          },
          items: {
            webId: 0,
            title: '',
            url: '',
            xpathTitle: '',
            xpathText: '',
            charset: ''
          },
          rules: {
            webId: [
              {required: true}
            ],
            title: [
              {required: true, message: '请输入标题', trigger: 'blur'}
            ],
            url: [
              {required: true, message: '请输入链接', trigger: 'blur'}
            ],
            xpathTitle: [
              {required: true, message: '请输入标题路径', trigger: 'blur'}
            ],
            xpathText: [
              {required: true, message: '请输入正文路径', trigger: 'blur'}
            ],
            charset: [
              {required: true, message: '请输入编码', trigger: 'blur'}
            ]
          },
          extraParams: {}
        },
        del: {
          modal: false,
          url: '',
          msg: '',
          params: null
        },
      },
      columns: [
        {
          type: 'index',
          title: '序号',
          align: 'center',
          width: 70
        },
        {
          title: '标题',
          key: 'title',
          render: (h, params) => {
            let self = this;
            let a  = h('a', {
              on: {
                click: function () {
                  let component = {
                    name: 'CrawlerShowNews',
                    desc: '新闻列表[' + params.row.title + ']',
                    show: true,
                    new: true,
                    params: {
                      categoryId: params.row.id
                    }
                  };
                  self.$store.dispatch('addComponent', component)
                  let param = {
                    categoryId: params.row.id
                  };
                  self.$axios.ajax(self.url.web.hasRead, param).then(function (response) {
                    if (response.data.errorCode === '0000') {
                      self.reload()
                    }
                  });
                }
              }
            }, params.row.title)
            let badge = h('Badge', {
              attrs: {
                count: params.row.newNum,
                type: 'success'
              },
              style:{
                marginLeft: '8px'
              }
            })
            return [a, badge]
          }
        },
        {
          title: '网址链接',
          key: 'url'
        },
        {
          title: '标题路径',
          key: 'xpathTitle'
        },
        {
          title: '正文路径',
          key: 'xpathText'
        },
        {
          title: '编码',
          key: 'charset'
        },
        {
          title: '操作',
          align: 'center',
          width: 150,
          render: (h, params) => {
            let self = this;
            let modify = h('a', {
              attrs: {
                style: 'margin-right: 2em;'
              },
              on: {
                click: function () {
                  self.category.form.modal = true;
                  self.category.form.title = '修改分类';
                  self.category.form.url = self.url.category.modify;
                  self.category.form.items.webId = params.row.webId;
                  self.category.form.items.title = params.row.title;
                  self.category.form.items.url = params.row.url;
                  self.category.form.items.xpathTitle = params.row.xpathTitle;
                  self.category.form.items.xpathText = params.row.xpathText;
                  self.category.form.items.charset = params.row.charset;
                  self.category.form.extraParams = {
                    id: params.row.id,
                  }
                }
              }
            }, '修改');
            let del = h('a', {
              on: {
                click: function () {
                  self.category.del.modal = true;
                  self.category.del.url = self.url.category.del;
                  self.category.del.msg = '分类标题：' + params.row.title;
                  self.category.del.params = {
                    categoryId: params.row.id
                  }
                }
              }
            }, '删除');
            return [modify, del]
          }
        }
      ]
    }
  },
  computed: {
    divStyle: function () {
      return {
        height: (this.$store.getters.tabs.height - 48) + 'px'
      }
    }
  },
  methods: {
    init: function () {
      let self = this;
      let menuLoan = this.params.menuLoanEntities;
      if (menuLoan.length === 0) {
        menuLoan = []
      } else {
        menuLoan = menuLoan.map(v => {return v.userId})
      }
      menuLoan.push(this.$store.getters.userInfo.id);
      let params = {
        userIds: menuLoan
      };
      this.$axios.ajax(this.url.web.query, params, {traditional: true}).then(function (response) {
        self.page = response.data.content
      });
    },
    pageChange (page) {
      this.pageNumber = page - 1;
      this.init()
    },
    pageSizeChange (pageSize) {
      this.pageSize = pageSize;
      this.init()
    },
    collapseChange (keys) {
      if (keys.length !== 0) {
        // 因为使用 accordion 模式，keys长度只会为 1
        let index = keys[0].slice(this.panelNamePrefix.length);
        this.category.webUrl = this.page.content[index].url
      }
    },
    web_add () {
      this.web.form.modal = true;
      this.web.form.title = '新增网站';
      this.web.form.url = this.url.web.add;
      this.web.form.items.url = '';
      this.web.form.items.title = '';
      this.web.form.extraParams = {}
    },
    web_modify (web) {
      this.web.form.modal = true;
      this.web.form.title = '修改网站';
      this.web.form.url = this.url.web.modify;
      this.web.form.items.url = web.url;
      this.web.form.items.title = web.title;
      this.web.form.extraParams = {
        id: web.id
      }
    },
    web_del (web) {
      this.web.del.modal = true;
      this.web.del.url = this.url.web.del;
      this.web.del.msg = '网站：' + web.title + '，其下所属的分类也会一并删除';
      this.web.del.params = {
        webId: web.id
      }
    },
    category_add (webId) {
      this.category.form.modal = true;
      this.category.form.title = '新增分类';
      this.category.form.url = this.url.category.add;
      this.category.form.items.title = '';
      this.category.form.items.url = '';
      this.category.form.items.xpathTitle = '';
      this.category.form.items.xpathText = '';
      this.category.form.items.charset = '';
      this.category.form.extraParams = {
        webId: webId
      }
    },
    computeCollapseStyle () {
      let divHeight = this.$refs[this.ref.rootDiv].clientHeight;
      let buttonsHeight = this.$refs[this.ref.buttons].clientHeight;
      let pageHeight = this.$refs[this.ref.page].$el.clientHeight;
      let contentHeight = divHeight - pageHeight - buttonsHeight;
      contentHeight = (contentHeight - 16) + 'px';
      this.collapseStyle = {
        overflowY: 'auto',
        height: contentHeight,
        border: '1px solid #e8eaec'
      }
    },
    webNewNum (web) {
      let num = 0;
        web.categories.forEach((v) => {
          num += v.newNum;
        });
      return num;
    },
    reload () {
      this.init()
    }
  },
  created () {
    this.init()
  },
  mounted () {
    this.computeCollapseStyle()
  },
  watch: {
    'params': function () {
      this.init()
    }
  }
}
</script>

<style scoped>

</style>



